:root {
  --color-primary: #69813b;
  --color-primary--dark: #69813b;
  --color-secondary: #b96a56;
  --font-body: gill-sans-nova, sans-serif;
  --font-headings: trajan-sans-pro, sans-serif;
}

hr {
  opacity: 1;
}

#page.site {
  margin-top: 112px;
}

a {
  text-decoration: none;
  color: var(--color-primary);
  transition: 0.3s;
}
a:hover {
  color: var(--color-primary--dark);
}

.arrow-link-white, .arrow-link {
  line-height: 1.75rem;
  align-self: flex-end;
  font-weight: 500;
  font-size: 1.75rem;
  z-index: 1;
}
.arrow-link-white:hover, .arrow-link:hover {
  text-decoration: underline;
}

.arrow-link-white {
  color: inherit;
}
.arrow-link-white:hover {
  color: inherit;
}

#header-img {
  position: relative;
}

.hero-headline {
  color: white;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  position: absolute;
  z-index: 3;
  font-weight: 400;
}
@media (min-width: 576px) {
  .hero-headline {
    text-align: center;
  }
}
.hero-headline__line-1 {
  font-size: clamp(16px, 4vw, 130px);
  font-family: var(--font-body);
  margin-inline-start: 8px;
}
@media (min-width: 576px) {
  .hero-headline__line-1 {
    margin-inline-start: unset;
  }
}
.hero-headline__line-2 {
  font-size: clamp(32px, 12vw, 183px);
  font-family: var(--font-headings);
}

.hero-headline {
  --vertical-center: 35%;
  --horizontal-center: 30%;
  --offset: 10%;
  --right: var(--offset);
  --bottom: var(--offset);
  --left: var(--offset);
  --top: var(--offset);
}
.hero-headline.bottom-right {
  text-align: start;
  bottom: var(--bottom);
  right: var(--right);
}
@media (min-width: 992px) and (max-width: 1199.98px) {
  .hero-headline.bottom-right {
    --bottom: 15%;
  }
}
.hero-headline.bottom-left {
  text-align: end;
  bottom: var(--bottom);
  left: var(--left);
}
@media (min-width: 992px) and (max-width: 1199.98px) {
  .hero-headline.bottom-left {
    --bottom: 15%;
  }
}
.hero-headline.top-right {
  text-align: start;
  right: var(--right);
  top: var(--top);
}
.hero-headline.top-left {
  text-align: end;
  left: var(--left);
  top: var(--top);
}
.hero-headline.center-left {
  text-align: end;
  bottom: var(--vertical-center);
  left: var(--left);
}
.hero-headline.center-right {
  text-align: start;
  bottom: var(--vertical-center);
  right: var(--right);
}
.hero-headline.center {
  text-align: center;
  bottom: var(--vertical-center);
  left: var(--horizontal-center);
}

p, body {
  font-family: var(--font-body);
  font-weight: 400;
  font-style: normal;
  font-size: 1.25rem;
}

p {
  font-size: 1.75rem;
  letter-spacing: 0.1rem;
}

h1,
.h1,
h2,
.h2 {
  font-weight: bold;
  text-transform: uppercase;
  color: var(--color-primary);
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  font-family: var(--font-headings);
}

.blue-topo-bg {
  background: url("/wp-content/uploads/2023/08/footer-bg.jpg");
  color: white;
}
.blue-topo-bg h2 {
  color: white;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.blue-topo-bg .form-arrow {
  position: absolute;
  right: 15px;
  top: 13px;
  z-index: 0;
}

.offset-topo-bg:after {
  content: "";
  position: absolute;
  margin-top: -350px;
  background: url("/wp-content/uploads/2023/08/light-gray-map-background.jpg");
  width: 100%;
  height: 700px;
  left: 0;
  z-index: -1;
}

.vertical-line, .vertical-line-rooms {
  border-right: 1.5px solid #414042;
  height: calc(100% - 152px);
  width: 58px;
  margin-top: 10px;
}
.vertical-line-white {
  border-right: 1.5px solid white;
}
.vertical-line-rooms {
  height: calc(100% - 136px);
}

.arrow {
  height: 163px;
  width: auto;
  left: 26px;
  z-index: 2;
  margin-top: -60px;
}
.arrow-white {
  filter: brightness(100) saturate(100%);
}

#containers {
  font-size: 1.25rem;
}
#containers p {
  font-size: 1.25rem;
}

.banner-bg-header {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.btn-white, .btn-reserve, .btn-default, .btn-secondary, .btn-diamond, .btn-diamond-white, .mega-menu__btn {
  border: 1px solid transparent;
  border-radius: 0;
  color: black;
  transition: 0.3s;
  text-decoration: none;
}
.btn-white:hover, .btn-reserve:hover, .btn-default:hover, .btn-secondary:hover, .btn-diamond:hover, .btn-diamond-white:hover, .mega-menu__btn:hover {
  color: white;
}
.btn-white:focus-visible, .btn-reserve:focus-visible, .btn-default:focus-visible, .btn-secondary:focus-visible, .btn-diamond:focus-visible, .btn-diamond-white:focus-visible, .mega-menu__btn:focus-visible {
  outline: var(--color-primary) 2px solid;
  outline-offset: 2px;
  border: 2px solid white;
}

/* ============ desktop view ============ */
@media all and (min-width: 992px) {
  .navbar .nav-item .dropdown-menu {
    display: none;
    margin-top: 0;
  }
  .navbar .nav-item:hover .dropdown-menu {
    display: block;
  }
}
/* ============ desktop view .end// ============ */
.navbar-nav .nav-item:hover > .dropdown-menu.depth_0, .navbar-nav .nav-item .dropdown-menu.depth_0.show {
  flex-direction: row;
  border-radius: 0;
  border: none;
  border-top: 4px solid var(--color-primary);
  top: auto;
  padding: 1rem;
}
.navbar-nav .nav-item:hover > .dropdown-menu.depth_0 ul, .navbar-nav .nav-item .dropdown-menu.depth_0.show ul,
.navbar-nav .nav-item:hover > .dropdown-menu.depth_0 li,
.navbar-nav .nav-item .dropdown-menu.depth_0.show li,
.navbar-nav .nav-item:hover > .dropdown-menu.depth_0 .dropdown-item,
.navbar-nav .nav-item .dropdown-menu.depth_0.show .dropdown-item {
  position: relative;
  border: none;
  border-radius: 0;
  font-weight: 300;
  font-family: var(--font-body);
  padding: 0;
}
.navbar-nav .nav-item:hover > .dropdown-menu.depth_0 > li > .dropdown-item, .navbar-nav .nav-item .dropdown-menu.depth_0.show > li > .dropdown-item {
  font-weight: 700;
  text-transform: uppercase;
  font-family: var(--font-headings);
  padding: 1rem 0;
}
.navbar-nav .nav-item:hover > .dropdown-menu.depth_0 li:last-child, .navbar-nav .nav-item .dropdown-menu.depth_0.show li:last-child {
  padding: 0;
}
@media (min-width: 992px) {
  .navbar-nav > .nav-item {
    margin-top: 2.5rem;
    padding-bottom: 2.5rem;
    text-transform: uppercase;
  }
}
.navbar-nav .nav-item {
  text-transform: none;
}
@media (min-width: 1200px) {
  .navbar-nav .nav-item:hover li:not(:nth-last-child(2)):not(:last-child) .dropdown-menu.depth_1 li {
    border-right: 1px solid black;
  }
}
@media (max-width: 1199.98px) {
  .navbar-nav .nav-item .dropdown-menu.depth_0.show {
    display: flex;
    flex-direction: column;
  }
  .navbar-nav .nav-item .dropdown-menu.depth_0.show > li > .dropdown-item {
    padding: 0.5rem 0;
  }
  .navbar-nav .nav-item .dropdown-menu.depth_0.show ul,
  .navbar-nav .nav-item .dropdown-menu.depth_0.show li {
    display: flex;
    flex-direction: column;
    font-size: 1.25rem;
  }
}
@media (min-width: 1200px) {
  .navbar-nav .nav-item:hover > .dropdown-menu.depth_0 {
    margin-top: 42px;
    display: grid;
    grid-template-columns: repeat(3, minmax(130px, 200px));
    grid-template-rows: 1fr;
    width: auto;
    height: auto;
    gap: 15px;
  }
  .navbar-nav .nav-item:hover > .dropdown-menu.depth_0 .dropdown-menu {
    width: min-content;
  }
}

.dropdown-toggle::after {
  display: none;
}

.header-bar {
  font-family: var(--font-headings);
  background: url(/wp-content/uploads/2023/07/nav-bg.jpg);
  background-size: cover;
  min-height: 100px;
  color: white;
}
.header-bar .logo.sm {
  height: 80px;
}
@media (min-width: 1400px) {
  .header-bar .logo.sm {
    max-height: 1000px;
  }
}
@media (max-width: 1199.98px) {
  .header-bar .nav-btn {
    margin-left: 1rem;
    margin-top: 0.5rem;
  }
}
.header-bar .nav-btn .nav-link {
  border: 1px solid white;
}
.header-bar .nav-btn .nav-link:hover {
  background: white;
  color: var(--color-primary);
}
.header-bar .nav-link {
  color: white;
  font-size: 1rem;
  margin-left: 0.25rem;
  margin-right: 0.25rem;
  display: inline-block;
  transition: 0.3s;
}
@media (min-width: 1400px) {
  .header-bar .nav-link {
    margin-left: 0.75rem;
    margin-right: 0.75rem;
  }
}
.header-bar .nav-link.active {
  color: white;
  font-weight: bold;
}
.header-bar li:not(.nav-btn) .nav-link.show,
.header-bar li:not(.nav-btn) .nav-link:hover {
  color: white;
}

.offcanvas {
  color: white;
}
.offcanvas ul li a:not(.mega-menu__btn) {
  padding: 0.75rem 1rem;
}
@media (max-width: 1199.98px) {
  .offcanvas {
    background: url(/wp-content/uploads/2023/07/nav-bg.jpg);
  }
}
@media (max-width: 991.98px) {
  .offcanvas {
    padding-bottom: 1rem;
  }
}

.mega-menu__image {
  aspect-ratio: 1;
  object-fit: cover;
}
.mega-menu__btn {
  background: var(--color-primary);
  color: white;
  border: 2px solid var(--color-primary);
  font-weight: 500;
  font-size: 1rem;
}
.mega-menu__btn:hover {
  background: white;
  color: var(--color-primary);
}
.mega-menu__event {
  width: unset;
}
.mega-menu__event-image {
  position: relative;
  z-index: 1;
}
.mega-menu__event-details {
  position: absolute;
  z-index: 2;
  background-color: var(--color-primary);
  color: white;
  display: grid;
  grid-template-columns: 10px 1fr;
  gap: 10px;
  bottom: 0;
}
.mega-menu__event-details--title {
  font-weight: 700;
  font-family: var(--font-gill-sans);
  grid-column: 1/-1;
  grid-row: 1/2;
}
.mega-menu__event-details--icon {
  grid-column: 1/2;
}
.mega-menu__event-details--time {
  grid-column: 2/3;
  grid-row: 2/3;
}

.title-bar.with-background {
  background: url("/wp-content/uploads/2023/08/light-gray-map-background.jpg");
}
@media (min-width: 992px) {
  .title-bar.with-background {
    margin-top: -90px;
  }
}
@media (max-width: 991.98px) {
  .title-bar.with-background {
    max-width: none;
  }
}
.title-bar label {
  padding-left: 1rem;
}
.title-bar .form-arrow {
  position: absolute;
  right: 15px;
  top: 13px;
  z-index: 0;
}

.btn-white {
  background: white;
  border-color: white;
}
.btn-reserve {
  color: var(--color-secondary);
  background: white;
  height: 43px;
  font-size: 1.14rem;
  line-height: 1.25;
  border: 2px solid white;
}
.btn-reserve:hover {
  border: 2px solid white;
}
.btn-default {
  color: var(--color-primary);
  border: 2px solid var(--color-primary);
  padding: 0.2rem 1rem 0.5rem;
  font-weight: 500;
  line-height: 3;
}
.btn-default:hover {
  background: var(--color-primary);
  color: white;
  border: 2px solid var(--color-primary);
}
.btn-secondary {
  color: white;
  border: 2px solid white;
  padding: 0.5rem 1rem;
  font-weight: 500;
}
.btn-secondary:hover {
  background: white;
  color: var(--color-primary);
}
.btn-diamond, .btn-diamond-white {
  transform: rotateZ(45deg);
  border-color: white;
}
.btn-diamond i, .btn-diamond-white i {
  color: white;
  transform: rotateZ(-45deg);
  transition: 0.3s;
}
.btn-diamond:hover, .btn-diamond-white:hover {
  background-color: white;
}
.btn-diamond:hover i, .btn-diamond-white:hover i {
  color: black;
}
.btn-diamond-white {
  background-color: white;
}
.btn-diamond-white i {
  color: var(--color-primary);
}
.btn-diamond-white:hover {
  background: var(--color-primary);
}
.btn-diamond-white:hover i {
  color: white;
}

footer {
  background-image: linear-gradient(to bottom, rgba(1, 1, 1, 0.2), rgba(1, 1, 1, 0.2)), url("/wp-content/uploads/2023/08/footer-bg.jpg");
  background-size: cover;
  background-position: center;
  color: white;
}
footer h2 {
  padding-bottom: 1rem;
}
footer p,
footer ul,
footer ol,
footer a,
footer li {
  font-family: var(--font-body);
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.1rem;
}
footer a {
  color: white;
  text-decoration: none;
  text-transform: none;
}
footer ul,
footer ol {
  list-style: none;
  padding-left: 0;
}
footer ul li,
footer ol li {
  margin-bottom: 0.5rem;
}
footer .social-icons a {
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}
footer .footer-info {
  background: #231f20;
}
@media (max-width: 991.98px) {
  footer .footer-info {
    text-align: center;
  }
}

.two-col--full-width {
  background-size: cover;
  background-position: bottom;
}
.two-col--full-width .two-col__subheadline p {
  color: white;
}

/** Eat & Drink Page **/
.menu-link {
  color: rgb(33, 37, 41);
}
.menu-link i {
  margin-right: 0.5rem;
}

ul.dining-hours {
  padding-left: 0;
  margin-bottom: 1.25rem;
  margin-top: 1rem;
}
ul.dining-hours li {
  list-style: none;
}

#weather-widget {
  color: white;
  padding: 1rem;
  font-size: 1.15rem;
}
#weather-widget .bi {
  --size: 2.25rem;
  width: var(--size);
  height: var(--size);
}
#weather-widget .row {
  background: url("/wp-content/uploads/2023/08/brown-basket-bg.jpg");
  background-size: cover;
}
#weather-widget .day {
  font-weight: 700;
  font-size: 1rem;
}
#weather-widget h2,
#weather-widget .h2 {
  color: white;
  font-size: 5rem;
}
#weather-widget h2 .bi,
#weather-widget .h2 .bi {
  --size: 3rem;
}
#weather-widget p {
  font-size: 1rem;
}
#weather-widget small {
  vertical-align: top;
  padding-left: 0.25rem;
}

/** Entertainment Page **/
.events-list-item {
  height: 450px;
  padding: 1rem 1.5rem;
}
.events-list-item-bg {
  padding-bottom: 1rem;
  padding-left: 1.5rem;
  background: url(/wp-content/uploads/2023/09/event-placeholder-bg.jpg);
}
.events-list-item h3 {
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 0.25rem;
  text-transform: uppercase;
}
.events-list-item p {
  font-size: 1rem;
  margin-bottom: 0;
}