.blue-topo-bg {
	background: url("/wp-content/uploads/2023/08/footer-bg.jpg");
	color: white;

	h2 {
		color: white;
		margin-top: 1rem;
		margin-bottom: 1rem;
	}

	.form-arrow {
		position: absolute;
		right: 15px;
		top: 13px;
		z-index: 0;
	}
}

.offset-topo-bg:after {
	content: "";
	position: absolute;
	margin-top: -350px;
	background: url("/wp-content/uploads/2023/08/light-gray-map-background.jpg");
	width: 100%;
	height: 700px;
	left: 0;
	z-index: -1;
}

.vertical-line {
	border-right: 1.5px solid #414042;
	height: calc(100% - 152px);
	width: 58px;
	margin-top: 10px;

	&-white {
		border-right: 1.5px solid white;
	}

	&-rooms {
		@extend .vertical-line;
		height: calc(100% - 136px);
	}
}

.arrow {
	height: 163px;
	width: auto;
	left: 26px;
	z-index: 2;
	margin-top: -60px;

	&-white {
		filter: brightness(100) saturate(100%);
	}
}

#containers {
	font-size: 1.25rem;
	p {
		font-size: 1.25rem;
	}
}

.banner-bg-header {
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}
